import { LoadDataButton, PreviewOptions } from './../../../models/component';
import { TranslateService } from '@ngx-translate/core';
import { StorageService } from './../../../services/storage-service';
import { Utility } from "./../../../services/utility";
import { Component, ViewChild, ChangeDetectorRef } from "@angular/core";
import { IonicPage, NavController, NavParams, Events, Content, Refresher } from "ionic-angular";
import { Session } from "../../../services/session";
import { ClubProvider } from "./../../../providers/club-provider";
import { pageCommon } from "../../page.base";
import { PreloadProvider } from '../../../providers/providers';

/**
 * Generated class for the CommunityPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

/**
 * 
 */
interface BoardColumn {
  Default: boolean | string | Array<any> | any;
  Club: boolean | string | Array<any>  | any;
  Board: boolean | string | Array<any>  | any;
  Video: boolean | string | Array<any>  | any;
}

@IonicPage()
@Component({
  selector: "page-community",
  templateUrl: "community.html"
})
export class CommunityPage extends pageCommon {
  @ViewChild(Refresher) refresher: Refresher;
  @ViewChild(Content) content: Content;
  @ViewChild("selectBox") selectBox: any;
  currentUser: any; // 当前用户信息
  Modules: any; // 菜单模块
  currentModule: string = ''; // 当前选中菜单模块
  tempModule: string = ''; // 当前选中菜单模块
  isLogin: Boolean = false; // 是否已经登录
  sites: any = []; // 厂区列表
  optionSite: string = ""; // 当前选中厂区  
  default: boolean = true;
  showSocial: boolean = false;
  //活动圈
  isShowError: boolean = false;
  DataEmpty: BoardColumn
  DataLoading: BoardColumn
  Datalist: BoardColumn
  DataButtons: BoardColumn;
  DataTranslateKey: BoardColumn;
  timer: BoardColumn
  finiteScroll = false;
  take = 20;
  selectOptions: { title: string } = { title: '' }
  skeleton = new Array(10).fill(0);
  loadButtons: Array<LoadDataButton>;
  constructor(
    public navCtrl: NavController,
    public session: Session,
    public navParams: NavParams,
    public events: Events,
    public clubProvider: ClubProvider,
    public storage: StorageService,
    public translateService: TranslateService,
    private preloadProvider: PreloadProvider,
    public cd: ChangeDetectorRef
  ) {
    super(navCtrl);
    this.initModules();
    this.initCommunity()
    this.initSubcribleEvent()
  }

  initModules() {
    this.Modules = [{ code: 'Default',
    displayName: this.translateService.instant('COMMUNITY_DISCOVER'),
    id: 1 }];
  }

  AppendModulesAfterClear(result: any) {
    this.initModules();
    if (result && result.length > 0) {
      this.Modules.push(...result);
    }
  }
  /**
   * 初始化
   */
  async initCommunity(isRefreshMenu?: boolean) { 
    try {
      this.isShowError = false
      this.DataEmpty = { Default: false, Club: false, Board: false, Video: false };
      this.DataLoading = { Default: true, Club: true, Board: true, Video: true };
      this.timer = { Default: null, Club: null, Board: null, Video: null };
      this.Datalist = { Default: [], Club: [], Board: [], Video: [] };
      await this._initColumn(isRefreshMenu) 
      await this._InfoConfig() 
      this.getBoardSites(); 

      for (let item of this.Modules) {
        this.getBoardByChannelCode(item.code)
      }


    } catch (e) {
      this.isShowError = true
      console.error(e)
    }

  }

  /**
   * 初始化头部菜单栏
   * 先等待Session中的数据
   * 等待时间过长则重新加载
   */
  async _initColumn(isRefreshMenu?: boolean) {
    return new Promise((resolve, reject) => {
      let waitingMiles = 15;
      if (isRefreshMenu) {
        this.getCommunityChannel(resolve, reject);
      } else {
        const waiting = () => {
          waitingMiles--;
          // console.log('enter waiting times:', waitingMiles)
          setTimeout(() => {
            if (this.session.communityColumn) {
              this.AppendModulesAfterClear(this.session.communityColumn);
              this.currentModule = this.Modules[0].code;
              resolve();
            } else if (waitingMiles === 0) {
              this.getCommunityChannel(resolve, reject);
            } else {
              waiting();
            }
          }, waitingMiles === 14 ? 0 : 500);

        };
        waiting();
      }
    });
  }


  /**
   * 初始化配置，个人信息，个人设置等
   */
  async _InfoConfig() {
    /** 设置 */
    this.selectOptions.title = this.translateService.instant('SOCIAL_CLUB_CHOOSE_AREA')
    // this.loadButtons = [{
    //   name: this.translateService.instant('SOCIAL_CLUB_GO_FOLLOW'),
    //   func: this.openRecommend,
    //   args: null
    // }]
    this.DataButtons = {
      Club : {
        name: this.translateService.instant('SOCIAL_CLUB_GO_FOLLOW'),
        func: this.openRecommend,
        args: null
      },
      Default : null,
      Board: null,
      Video: null
    };
    this.DataTranslateKey = {
      Club: 'SOCIAL_CLUB_NO_FOLLOW',
      Default : null,
      Board: null,
      Video: null
    };
    if (this.session.user != null) {
      this.currentUser = this.session.user;
      this.isLogin = true;
    }
    if (this.currentUser && this.currentUser.employeeInfo) {
      this.optionSite = this.optionSite || this.currentUser.employeeInfo.siteCode
      this.currentUser.pictureUrl = this.currentUser.pictureUrl || "assets/img/avatar-round.png";
    }

    /** 加载tabs时，已经取到本地储存中 showSocial 的值，优先取本地储存的；如没有则取后台设置的值 */
    if (typeof this.session.showSocial === 'boolean') {
      this.showSocial = this.session.showSocial;
    } else {
      if (this.currentUser) {
        this.showSocial = Utility.boolean(this.currentUser.showSocial);
      }
    }

    /** 不支持该模块时，显示Coming soon界面 */
    this.default = !(
      this.session.language == "zh-CN" || this.session.language == "zh-TW"
    );

    /** 获取本地储存的siteCode */
    let siteCode = await this.storage.getItem('siteCode');
    if (siteCode) {
      this.optionSite = siteCode
    }
    this.content && this.content.resize()
  }

  /** 初始化订阅事件 */
  initSubcribleEvent() {
    let eventList = [ 
      {
         eventName: "lang:changed",
         isRefreshMenu: true,
         timer: null
      },
      {
        eventName: "user:info-changed",
        isRefreshMenu: true,
        timer: null
      },
      {
        eventName: "user:permission-changed",
        isRefreshMenu: false,
        timer: null
      },
      {
        eventName: "MassFlagChange",
        isRefreshMenu: false,
        timer: null
      },
      {
        eventName: "changeCommunityUrl",
        isRefreshMenu: false,
        timer: null
      }]
    eventList.forEach((event, index) => {
      let eventName = event.eventName
      let arg = event.isRefreshMenu
      this.events.subscribe(eventName, () => {
        this.refreshCurrent(true)
        event.timer = setTimeout(() => {
          // if('lang:changed' == eventName){ // 如果是切換語言事件，不觸發 user:info-changed了，
          //   let userchange = eventList.find(p => p.eventName == 'user:info-changed')
          //   setTimeout(()=>{
          //     clearTimeout(userchange.timer)
          //   },1)
            
          // }
          this.initCommunity(arg)
        }, (index + 1) * 100) 
      });
    });

  }

  /**
   * 获取Channel 
   * @param resolve 
   * @param reject 
   */
  getCommunityChannel(resolve, reject) {
    let repeatTimes = 20;
    const wait = () => {
      repeatTimes--;
      if (repeatTimes == 0) {
        reject()
      }
      setTimeout(() => {
        if (this.session.userId) {
          this.clubProvider.getCommunityChannel().subscribe(data => {
            this.AppendModulesAfterClear(data.result);
            this.currentModule = this.Modules[0].code
            resolve()
            if (!(this.cd as any).destroyed) {this.cd.detectChanges(); }
          }, err => {
            reject(err)
          })
        } else {
          wait()
        }
      }, 500)
    }
    wait()
  }

  /**
   * 
   * @param e 
   */
  receiveVal(e: LoadDataButton) {
    e && e.func.apply(this, e.args)
  }

  getDefaultContent() {
    this.clubProvider.getCommunityContent(this.optionSite, 0, this.take);
  }

  doInfinite($event) {
    let skip = 0;
    if (this.Datalist.Default instanceof Array) {
      skip = this.Datalist.Default.length;
    }
    this.clubProvider.getCommunityContent(this.optionSite, skip, this.take).subscribe((res) => {
      if (this.Datalist.Default instanceof Array) {
        this.finiteScroll = res.isMore;
        this.Datalist.Default.push(...res.result);
      }
    }, (er) => {}, () => {
      $event.complete();
    });
  }

  /**
   * 根据ChannelCode 获取对应社团
   * @param channelCode 
   */
  getBoardByChannelCode(channelCode: string) {
    this.DataEmpty[channelCode] = false
    this.DataLoading[channelCode] = true
    if (channelCode === 'Default') {
      this.DataLoading[channelCode] = true;
      this.clubProvider.getCommunityContent(this.optionSite, 0, this.take).subscribe((res) => {
        this.finiteScroll = res.isMore;
        this.Datalist.Default = res.result;
        if (res.result.length === 0) {
          this.DataEmpty[channelCode] = true;
        }
      }, err => {}, () => {
        this.DataLoading[channelCode] = false;
      });
      return;
    }

    this.timer[channelCode] = setTimeout(() => { this.DataLoading[channelCode] = true }, 300)
    this.Datalist[channelCode] = []
    this.clubProvider.getMyClubs(this.optionSite, channelCode).subscribe(res => {
      if (res.result && res.result.length > 0) {
        this.Datalist[channelCode] = res.result
      } else {
        this.DataEmpty[channelCode] = true
      }
    }, err => {
    }, () => {
      clearTimeout(this.timer[channelCode])
      this.DataLoading[channelCode] = false
    });
  }

  /**
   * 获取厂区
   */
  getBoardSites() {
    this.clubProvider.BoardSites().subscribe(res => {
      this.sites = res.result;
    });
  }

  /**
   * 刷新当前数据
   */
  refreshCurrent(isRefresh) {
    if (this.currentUser && this.currentUser.employeeInfo) {
      this.optionSite = this.optionSite || this.currentUser.employeeInfo.siteCode || 'LF';
    }

    this.storage.setItem('siteCode', this.optionSite);

    this.clubProvider.getCommunityChannel().subscribe(data => {
      // this.Modules = data.result;
      this.AppendModulesAfterClear(data.result);
      this.currentModule = this.tempModule || this.Modules[0].code;

      this.getBoardSites();
      for (const item of this.Modules) {
        this.getBoardByChannelCode(item.code);
      }
      if (!(this.cd as any).destroyed) {this.cd.detectChanges(); }
    }, err => {
    });
  }

  /**
   * 下拉刷新
   * @param _event 
   */
  doRefresh(_event) {
    _event.complete();
    this.refreshCurrent(true)
  }

  // **打开帖子详情 */
  openPostDetail(boardId: number) {
    this.clubProvider.openWebpage(this.clubProvider.PostDetail(boardId), this.navCtrl);
  }
     // **打开社团详情 */
     openClubDetailFromDefault(boardId: number, channelCode: string) {
      event.stopPropagation();
      event.preventDefault();
      this.openClubDetail(boardId, channelCode);
   }
  /**打开社团详情 */
  openClubDetail(boardId: number, channelCode: string) {
    this.clubProvider.openWebpage(this.clubProvider.BoardDetail(boardId, channelCode, this.optionSite), this.navCtrl)
  }
  // **打开个人主页详情 */
  openCreatorPage(userId: number) {
    event.stopPropagation();
    event.preventDefault();
    this.clubProvider.openWebpage(this.clubProvider.UserPage(userId), this.navCtrl);
  }

  /**
   * 打开全部社团
   */
  openRecommend() {
    this.clubProvider.openWebpage(this.clubProvider.AllBoards(this.optionSite), this.navCtrl)
  }

  /**
   * 前往搜索
   * @param _event 
   */
  goSearching(_event) {
    (_event || event).stopPropagation();
    this.navCtrl.push('SearchPage')
  }

  /**
   * 切换当前module
   * @param item 
   */
  toggleModule(item) {
    this.currentModule = item.code;
    this.tempModule =  item.code;
  }

  selectedText() {
    if (!this.optionSite || this.sites.length === 0 ) {
      return '';
    }
    return this.sites.find(p => p.code === this.optionSite).displayName;
  }

  previewImg(item, index: number) {
    event.stopPropagation();
    const options: PreviewOptions = {
      pictures: item.imgs.map(x => {
        return {
          pictureUrl: x.sourceUrl,
          // id: x.id
        };
      }),
      index
    };
    this.preloadProvider.previewImage(options.pictures,options.index);
  }
  
} 


